*{position:relative}html{scroll-behavior:smooth}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#5D81F8;font-family:"Manrope",sans-serif;box-sizing:border-box}button{width:250px;height:50px;border-radius:9px;border:0 solid rgba(0,0,0,0);background:#F2F7FC;color:#5D81F8;font-size:16px;font-weight:600;cursor:pointer;margin:2rem;outline:none;transition:0.2s}h5{color:#F8A412;margin-top:4rem;text-transform:uppercase;font-weight:400;font-size:16px}h1,p{max-width:600px;margin:auto}.imageDiv{max-width:100%;max-height:100%;object-fit:cover}.imageDiv img{width:100%;height:100%;object-fit:cover}

