#nav-container{box-sizing:border-box;position:fixed;width:100vw;transition:0.3s;opacity:1;z-index:1000}#nav-container #navbar{margin:0 2rem 0 0;width:100vw;height:100%;display:flex;justify-content:space-between;align-items:center;font-weight:500;text-transform:uppercase}#nav-container #navbar #brand{margin-left:2rem;font-weight:500;color:#35357E}#nav-container #navbar #brand span{font-weight:900}#nav-container #navbar #navlinks{margin:0}#nav-container #navbar #navlinks ul{width:700px;list-style:none;margin:0 70px;padding:0;display:flex}#nav-container #navbar #navlinks ul .navItem{position:relative;cursor:pointer;width:130px;text-align:center;height:40px;display:flex;align-items:center}#nav-container #navbar #navlinks ul .navItem .navLink{overflow:hidden}#nav-container #navbar #navlinks ul .navItem .navLink a{text-decoration:none;color:#fff}#nav-container #navbar #navlinks ul .navItem .navLink .navLine{width:1.5rem;height:2px;background-color:#fff;transition:0.2s;transform:translatex(-100%)}#nav-container #navbar #navlinks ul .navItem .navLink:hover .navLine{transform:translate(0)}.navTop{height:100px;background:rgba(150,132,146,0)}.navScroll{height:50px;background:#fff;color:#F8A412;box-shadow:0px 2px 5px 0px rgba(53,53,126,0.75)}.navScroll #navbar #navlinks ul .navLink a{color:#35357E !important}.navScroll #navbar #navlinks ul .navLink div{background-color:#35357E !important}.navScroll #navbar #navlinks .hamburger .hamburgerBox .hamburgerInner,.navScroll #navbar #navlinks .hamburger .hamburgerBox .hamburgerInner::before,.navScroll #navbar #navlinks .hamburger .hamburgerBox .hamburgerInner::after{background-color:#35357E}.hamburger{cursor:pointer;display:inline-block;border:0;margin:0;z-index:10;color:#e6e6fa;outline:none;transition:transform 0.1s ease-in-out}.hamburger .hamburgerBox{position:relative;width:25px;height:25px;display:inline-block}.hamburger .hamburgerBox:hover .hamburgerInner::before{top:-12px}.hamburger .hamburgerBox:hover .hamburgerInner::after{top:12px}.hamburger .hamburgerBox .hamburgerInner{width:100%;height:4px;background-color:#fff;position:absolute;left:0;top:50%;transform:translateY(-50%);transition:background-color .2s}.hamburger .hamburgerBox .hamburgerInner::before,.hamburger .hamburgerBox .hamburgerInner::after{width:100%;height:4px;background-color:#fff;position:absolute;left:0;content:'';transition:.2s}.hamburger .hamburgerBox .hamburgerInner::before{top:-8px}.hamburger .hamburgerBox .hamburgerInner::after{top:8px}#sidebarBackground{width:100vw;height:100vh;position:fixed;top:0;left:0;z-index:-10;transition:0.2s}.backgroundActive{background-color:rgba(0,0,0,0)}.backgroundActiveBlack{background-color:rgba(0,0,0,0.5)}.backgroundDisabled{background-color:rgba(0,0,0,0.5);display:none}

.sidebar{box-sizing:border-box;position:fixed;width:330px;height:100vh;background-color:#fff;transform:translateX(100vw);transition:0.3s;z-index:10000;top:0;left:0;padding:30px 50px;cursor:default;color:#5D81F8}.sidebar .navIcon{width:100%;text-align:right;margin-bottom:40px}.sidebar .navIcon .icon{color:#5D81F8;cursor:pointer}.sidebar ul{color:#5D81F8;display:flex;flex-direction:column;height:300px;text-align:left}.sidebar ul li{overflow:hidden}.sidebar ul li a{text-decoration:none;color:#5D81F8}.sidebar ul li #navSocials{width:100%;display:flex;align-items:space-between;margin-top:3rem}.sidebar ul li #navSocials .navIcon{margin:0;text-align:left;font-size:10px}.sidebar ul li .sideLine{width:1.5rem;height:3px;background-color:#5D81F8;transition:0.2s;transform:translatex(-100%)}.sidebar ul li:hover .sideLine{transform:translate(0)}.sidebar ul .projectList{display:flex;justify-content:space-between}.sidebar ul .projectList .icon{cursor:pointer}.sidebarActive{width:330px;transition:0.3s;transform:translateX(calc(100vw - 330px))}

#header{position:relative;height:100vh;min-height:600px;z-index:10;background:repeating-linear-gradient(90deg, rgba(255,255,255,0.5), rgba(255,255,255,0.5) 1px, transparent 0px, transparent 130px),linear-gradient(#5D81F8, #B5EDFF);background-size:3750px 100%;background-position:right}#header #helloText{padding-top:30vh;padding-left:20vw;text-align:left;width:400px;font-size:36px;font-weight:900;color:#35357E}#header #helloText button{color:#F8A412;margin:0;display:block;margin-top:100px}#header #helloText button:hover{color:#fff;background:#F8A412}#header #helloText #headerImg{position:absolute;left:calc(100vw - 900px);bottom:-200px}

