.imageDiv{position:relative;background:#F8A412;overflow:hidden}.imageDiv .imageContainer{position:absolute;width:100%;height:100%;background:rgba(53,53,126,0.8);top:0;left:0;display:flex;opacity:0.001;transition:0.2s}.imageDiv .imageContainer .imageText{color:#fff;position:absolute;bottom:-50px;left:20px;transition:0.3s;text-align:left;opacity:0;z-index:6}.imageDiv .imageContainer .imageText h2,.imageDiv .imageContainer .imageText h4{margin:0;padding:0}.imageDiv .imageContainer:hover{opacity:1}.imageDiv .imageContainer:hover .imageText{bottom:20px;opacity:1}.imageDiv .imageContainer .icon{color:dodgerblue;margin:auto;cursor:pointer}

