#skills{width:100%;display:flex;flex-direction:column;padding-top:250px;padding-bottom:3rem;background:repeating-linear-gradient(90deg, rgba(93,129,248,0.5), rgba(93,129,248,0.5) 1px, transparent 0px, transparent 150px),linear-gradient(#F2F7FC, #FFE0B2);background-size:3600px 100%;background-position:center;z-index:4;position:relative;align-items:center}#skills #skillsTitles{width:494px;text-align:left;margin-top:4rem;text-align:center}#skills #skillsTitles h1{color:#F8A412}#skills #skillsContainer{padding:50px 0;width:1200px;display:flex;justify-content:space-around}

