@import "./sass-variables.sass";

#nav-container {
    box-sizing: border-box;
    position: fixed;
    width: 100vw;
    // padding: 0 2rem 0 2rem
    transition: 0.3s;
    opacity: 1;
    z-index: 1000;
    #navbar {
        margin: 0 2rem 0 0;
        width: 100vw;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-weight: 500;
        text-transform: uppercase;
        #brand {
            // width: 46%
            margin-left: 2rem;
            font-weight: 500;
            color: $cfour;
            // border: 2px solid green
            span {
                font-weight: 900; } }
        #navlinks {
            // width: 50%
            margin: 0;
            // border: 2px solid red
            ul {
                width: 700px;
                list-style: none;
                margin: 0 70px;
                padding: 0;
                display: flex;
                // justify-content: space-around
                .navItem {
                    // margin: 0px 40px 0px 0px
                    position: relative;
                    cursor: pointer;
                    width: 130px;
                    text-align: center;
                    height: 40px;
                    display: flex;
                    // justify-content: center
                    align-items: center;
                    // border: 2px solid yellow

                    .navLink {
                        overflow: hidden;
                        a {
                            text-decoration: none;
                            color: $clight; }

                        .navLine {
                            width: 1.5rem;
                            height: 2px;
                            background-color: $clight;
                            // position: absolute
                            transition: 0.2s;
                            transform: translatex(-100%); }
                        &:hover {
                            .navLine {
                                transform: translate(0); } } } } } } } }

.navTop {
    height: 100px;
    background: rgba(150,132,146,0); }

.navScroll {
    height: 50px;
    background: $clight;
    color: $cthird;
    box-shadow: 0px 2px 5px 0px rgba($cfour, 0.75);
    #navbar {
        #navlinks {
            ul {
                .navLink {
                    a {
                        color: $cfour !important; }
                    div {
                        background-color: $cfour !important; } } }
            .hamburger {
                .hamburgerBox {
                    .hamburgerInner,
                    .hamburgerInner::before,
                    .hamburgerInner::after {
                        background-color: $cfour; } } } } } }

.hamburger {
    // padding: 10px
    cursor: pointer;
    display: inline-block;
    border: 0;
    margin: 0;
    z-index: 10;
    color: $cmain;
    outline: none;
    transition: transform 0.1s ease-in-out;

    .hamburgerBox {
        position: relative;
        width: 25px;
        height: 25px;
        display: inline-block;

        &:hover .hamburgerInner {
 }            // background-color: white
        &:hover .hamburgerInner::before {
            top: -12px; }

        &:hover .hamburgerInner::after {
            top: 12px; }

        .hamburgerInner {
            width: 100%;
            height: 4px;
            background-color: $clight;
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            transition: background-color .2s; }

        .hamburgerInner::before,
        .hamburgerInner::after {
            width: 100%;
            height: 4px;
            background-color: $clight;
            position: absolute;
            left: 0;
            content: '';
            transition: .2s; }

        .hamburgerInner::before {
            top: -8px; }

        .hamburgerInner::after {
            top: 8px; } } }

#sidebarBackground {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -10;
    transition: 0.2s;
 }    // background-color: rgba(0,0,0,0.5)

.backgroundActive {
    background-color: rgba(0,0,0,0);
 }    // z-index: 500

.backgroundActiveBlack {
    background-color: rgba(0,0,0,0.5);
 }    // z-index: 500

.backgroundDisabled {
    background-color: rgba(0,0,0,0.5);
    // z-index: 500
    display: none; }
