@import "./sass-variables.sass";

.imageDiv {
    position: relative;
    // box-shadow: 4px 1px 5px 1px rgba(0,0,0,0.2)
    background: $cthird;
    overflow: hidden;
    .imageContainer {
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgba($cfour,0.8);
        top: 0;
        left: 0;
        display: flex;
        opacity: 0.001;
        transition: 0.2s;
        // overflow: hidden
        .imageText {
            // opacity: 1
            color: $clight;
            position: absolute;
            bottom: -50px;
            left: 20px;
            transition: 0.3s;
            text-align: left;
            opacity: 0;
            z-index: 6;
            h2, h4 {
                margin: 0;
                padding: 0; } }

        &:hover {
            opacity: 1;
            .imageText {
                bottom: 20px;
                opacity: 1; } }
        .icon {
            color: dodgerblue;
            margin: auto;
            cursor: pointer;
 } } }            // background: $clight
