@import "./sass-variables.sass";

#footer {
    min-height: 50vh;
    background: $lin2;
    background-size: $bs1;
    background-position: center;
    color: $clight;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font-size: 15px;
    #mainFooter {
        width: 80%;
        max-width: 1200px;
        text-align: left;
        line-height: 30px;
        // color: $cfooter
        display: flex;
        height: 400px;
        align-items: center;
        justify-content: space-between;
        #footerPhoto {
            height: 179px;
            width: 179px;
            background: $clight;
            border-radius: 50%;
            margin: 94.5px;
            overflow: hidden;
            position: relative;
            img {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translateX(-63%) translateY(-50%);
                height: 100%; } }
        #meSocialsContainer {
            #socialsFoot {
                .icon {
                    margin: 35px;
                    cursor: pointer;
                    transition: 0.3s;

                    &:hover {
                        color: $cthird; } } } }
        // #linksContactContainer
        //     display: flex

        #contactFoot {
            padding-bottom: 2rem;
            h1 {
                text-align: left;
                margin: 0;
                padding: 0; }
            p {
                white-space: nowrap;
                margin: 0;
                .icon {
                    padding-right: 0.5rem; } }
            a {
                text-decoration: none;
                color: $clight;
                &:hover {
                    color: $cthird; } } } }
    #copyrights {
        font-family: $ftwo;
        height: 100px;
        display: flex;
        text-align: left;
        width: 100%;
        p {
            width: 80%;
            max-width: $maxWidth;
            margin: auto;
            // height: 100%
            color: $clight; } } }
