@import "./sass-variables.sass";

#aboutMe {
    background-color: $clight;
    width: 100%;
    color: $csecond;
    position: relative;
    display: flex;
    flex-direction: column;
    jjustify-content: center;
    tex-align: center;
    #amInfo {
        display: flex;
        flex-direction: column;
        align-items: center;
        h5 {
            padding-top: 4rem;
            color: $csecond;
            margin-top: 0;
            display: inline-block; }
        h1 {
            margin-bottom: 1rem; }
        p {}
        #aboutList {
            width: 490px;
            text-align: left;
            margin: auto;
            list-style: none;
            padding-bottom: 300px;
            li {
                margin-top: 40px;
                display: flex;
                // align-items: center
                font-size: 50px;
                // border: 2px solid yellow

                p {
                    width: 440;
                    font-size: 16px;
 }                    // border: 2px solid green

                span {
                    min-width: 10px;
                    height: 10px;
                    background-color: $cthird;
                    margin-right: 10px;
                    margin-top: 7px;
                    border-radius: 50%;
 } } } }                    // border: 2px solid red
    #experienceContainer {
        position: absolute;
        z-index: 5;
        left: 50%;
        transform: translate(-50%);
        bottom: -300px;
        #experience {
            width: 700px;
            background: $lin4;
            margin: auto;
            position: relative;
            margin-top: 150px;
            display: flex;
            flex-direction: column;
            align-items: center;
            h1 {
                color: $csecond;
                position: absolute;
                top: -50px;
                left: -50px;
                span {
                    color: $clight; } }
            #experienceList {
                width: 490px;
                text-align: left;
                margin: auto;
                list-style: none;
                color: $clight;
                padding-top: 60px;
                padding-bottom: 50px;
                li {
                    padding-top: 40px;
                    display: flex;
                    // align-items: center
                    font-size: 16px;

                    p {
                        width: 440; }

                    span {
                        min-width: 10px;
                        height: 10px;
                        background-color: $cthird;
                        margin-right: 10px;
                        margin-top: 7px;
                        border-radius: 50%; } } }

            button {
                background: rgba(0,0,0,0);
                border: 1px solid $clight;
                color: $clight;
                font-size: 16px;
                &:hover {
                    background: $clight;
                    color: #A14DD9; } }
            #experienceImg {
                position: absolute;
                left: 65%;
                top: -60%; } } } }

